/** Bootstrap navbar fix (https://git.io/fADqW) */
.comment-list ol {
  list-style: none;
}

.navbar-nav .menu-item.dropdown .dropdown-menu {
  position: absolute;
  left: 0;
  top: 100%;
}
.navbar-nav .menu-item.dropdown:hover .dropdown-menu {
  display: block;
}

/** Search form */
.search-form label {
  font-weight: normal;
}

/**
 * WordPress Generated Classes
 * @see http://codex.wordpress.org/CSS#WordPress_Generated_Classes
 */
/** Media alignment */
.alignnone {
  margin-left: 0;
  margin-right: 0;
  max-width: 100%;
  height: auto;
}

.aligncenter {
  display: block;
  margin: 0.5rem auto;
  height: auto;
}

.alignleft,
.alignright {
  margin-bottom: 0.5rem;
  height: auto;
}

@media (min-width: 576px) {
  .alignleft {
    float: left;
    margin-right: 0.5rem;
  }

  .alignright {
    float: right;
    margin-left: 0.5rem;
  }
}
/** Captions */
/** Text meant only for screen readers */
header.banner {
  z-index: 9;
}

body#tinymce {
  margin: 12px !important;
}

.video-background {
  z-index: 2;
  overflow: hidden;
}
.video-background .content {
  width: 100%;
  height: 100%;
}
.video-background video {
  position: relative;
  z-index: -1;
  left: 50%;
  transform: translateX(-50%);
  width: auto;
  height: 115vh;
  pointer-events: none;
}
.video-background .ratio {
  height: 100%;
  z-index: -1;
}
.video-background .ratio > * {
  top: 50%;
  left: 50%;
  width: 160%;
  height: 100vw;
  transform: translate(-50%, -50%);
}